*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
}
ul{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    padding: 16px;
    background-color: rgb(38, 36, 36);
    color: white;
}
.links{
    text-decoration: none;
    text-transform: capitalize;
    color: white;
    margin: 0 16px;
}
.links.active{
    color: red;
}
li{
    list-style-type: none;
}
span{
    color: red;
}
.card{
    display: flex;
    flex-direction: column;
    /* justify-content: center;
    align-items: center; */
    padding: 10px;
    background-color: white;
    margin: 10px;
    width: 300px; 
    box-shadow: 0 2px 5px 0px rgba(0, 0, 0, 0.4);   
    border: 1px solid #ddd; 
}
.card img{
    max-width: 100%; 
  height: 200px; 
  
}
.heading{
        font-size: 14px;
        margin-top: 0; 
}
.content{
    padding: 4px;
    color: beige;
    font-size: 12px;
}
.anchor{
    text-decoration: none;
    padding: 6px;
    background-color: blue;
    color:white;
    margin-top: 20px;
    border-radius: 6px;
    float: right;
}
.body{
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 100%;
    flex-wrap: wrap;  
}

@media screen and (max-width:720px){
    ul{
        background-color:black;
        width: 100vw;
        display: flex;
        flex-wrap: wrap;
    }
    .links{
        display: flex;
        flex-wrap: wrap;
        background-color: darkgray;
        color: white;
        padding: 4px;
        border-radius: 4px;
        margin: 10px;
    }
    .links.active{
        background-color: blue;
        color: white;
    }
}


